.splash-screen-2-child {
  width: 375px;
  height: 812px;
  position: relative;
  border-radius: var(--br-11xl);
  background-color: var(--color-white);
  display: none;
  max-width: 100%;
}
.spare-png-1 {
  align-self: stretch;
  height: 81px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
  z-index: 1;
}
.home-indicator1 {
  height: 5px;
  flex: 1;
  position: relative;
  border-radius: var(--br-81xl);
  background-color: var(--color-black);
  z-index: 1;
}
.home-indicator-container,
.splash-screen-2 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.home-indicator-container {
  align-self: stretch;
  flex-direction: row;
  padding: 0 55px 0 var(--padding-28xl);
}
.splash-screen-2 {
  width: 100%;
  position: relative;
  border-radius: var(--br-11xl);
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: column;
  padding: 363px var(--padding-47xl) var(--padding-7xs) 73px;
  box-sizing: border-box;
  gap: 357px;
  line-height: normal;
  letter-spacing: normal;
}
